import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'
import { navigate } from 'gatsby-link'
import scrollTo from 'gatsby-plugin-smoothscroll'
import AppBar from '@material-ui/core/AppBar'
import Drawer from '@material-ui/core/Drawer'
import Grid from '@material-ui/core/Grid'
import Hidden from '@material-ui/core/Hidden'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
import Navigation from '../navigation/navigation'
import branding from '../../../images/logos/compose-us_branding.png'
import { sections } from '../../../lib/menu-nav'
import withStyles from './header.style'

const Header = ({ siteTitle, classes }) => {
  const [drawerState, updateDrawerState] = React.useState(false)

  const navCallBack = (param) => {
    const url = window.location.href
    const path = param.replace('/', '')

    if (url.indexOf('blog') < 0 && url.indexOf('impressum') < 0) {
      if (param.indexOf('/#') >= 0) {
        scrollTo(path)
      } else {
        navigate(param)
      }
    } else {
      navigate(param)
    }
  }

  const handleToggleDrawer = () => {
    updateDrawerState(!drawerState)
  }

  return (
    <React.Fragment>
      <AppBar position="sticky" className={classes.root}>
        <Grid container justify={'space-between'}>
          <Grid item className={classes.item}>
            <Link
              to="/"
              className={classes.link}
              aria-label={'compose.us logo'}
            >
              <img
                src={branding}
                className={classes.image}
                alt="compose.us logo"
              />
            </Link>
          </Grid>
          <Grid item className={classes.item}>
            <Hidden mdUp>
              <Drawer
                open={drawerState}
                anchor="left"
                onBackdropClick={() => handleToggleDrawer()}
                onEscapeKeyDown={() => handleToggleDrawer()}
              >
                <div
                  className={classes.drawer}
                  role="presentation"
                  onClick={() => updateDrawerState(!drawerState)}
                  onKeyDown={() => handleToggleDrawer()}
                >
                  <Navigation
                    sections={sections}
                    callback={(param) => navigate(param)}
                    aria-label={`Navigation for: ${siteTitle}`}
                  />
                </div>
              </Drawer>
              <IconButton
                className={classes.iconButton}
                onClick={() => updateDrawerState(!drawerState)}
              >
                <MenuIcon />
              </IconButton>
            </Hidden>
            <Hidden smDown>
              <Navigation
                sections={sections}
                callback={(param) => navCallBack(param)}
                aria-label={`Navigation for: ${siteTitle}`}
              />
            </Hidden>
          </Grid>
        </Grid>
      </AppBar>
    </React.Fragment>
  )
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default withStyles(Header)
