import React from 'react'
import ReactGA from 'react-ga'
import PropTypes from 'prop-types'

import Button from '@material-ui/core/Button'
import Icon from '@material-ui/core/Icon'
import Snackbar from '@material-ui/core/Snackbar'
import CloseIcon from '@material-ui/icons/Close'
import CheckIcon from '@material-ui/icons/Check'
import { useCookies } from 'react-cookie'

const reactGA = gaProp => {
  ReactGA.initialize(gaProp)
  ReactGA.pageview(window.location.pathname + window.location.search)
}

const GaSnackbar = ({ gaProperty, message, labels }) => {
  const [cookies, setCookie] = useCookies()
  const consentCookie = 'cu-consent'
  const [openSnackbar, setOpenSnackbar] = React.useState(false)

  React.useEffect(() => {
    // Only show the snackbar when the cookie is not set
    if (!cookies[consentCookie]) {
      setOpenSnackbar(true)
    } else {
      if (cookies[consentCookie] === 'true') {
        reactGA(gaProperty)
      }
    }
  }, [cookies, gaProperty])

  const handleOptOut = () => {
    document.cookie = consentCookie + '=false'
    setOpenSnackbar(false)
    setCookie(consentCookie, false)
  }

  const handleConsent = () => {
    setOpenSnackbar(false)
    setCookie(consentCookie, true)
    reactGA(gaProperty)
  }

  return (
    <Snackbar
      anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      key={`gao-message-id`}
      open={openSnackbar}
      ContentProps={{
        'aria-describedby': 'consent-snackbar',
      }}
      aria-describedby="Google Analytics Opt Out"
      message={
        <span id="consent-snackbar" style={{ textTransform: 'uppercase' }}>
          <Icon className={'Info'} />
          {message}
        </span>
      }
      action={[
        <Button
          key={'close'}
          aria-label={labels.consent}
          color={'primary'}
          onClick={handleConsent}
        >
          {labels.consent}
          <CheckIcon />
        </Button>,
        <Button
          key={'opt-out'}
          aria-label={labels.optOut}
          color={'primary'}
          onClick={handleOptOut}
        >
          {labels.optOut}
          <CloseIcon />
        </Button>,
      ]}
    />
  )
}

GaSnackbar.propTypes = {
  gaProperty: PropTypes.string.isRequired,
  message: PropTypes.string.isRequired,
  labels: PropTypes.object,
}

export default GaSnackbar
