const RandomFooterText = () => {
  const adjs = [
    'a few',
    'cool',
    'creative',
    'funny',
    'great',
    'helpful',
    'interesting',
    'loving',
    'multiple',
    'nice',
    'real',
    'total',
    'understanding',
  ]
  const subs = [
    'coders',
    'developers',
    'digital nomads',
    'geeks',
    'humans',
    'kids',
    'learners',
    'monkeys',
    'nerds',
    'optimists',
    'programmers',
  ]
  const locations = [
    'Bavaria',
    'Germany',
    'Europe',
    'Landshut',
    'Passau',
    'around the world',
  ]

  const randomOf = arr => {
    return arr[Math.floor(Math.random() * arr.length)]
  }

  return ` created by ${randomOf(adjs)} ${randomOf(subs)} from ${randomOf(
    locations
  )} `
}

export default RandomFooterText
