import React from 'react'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import withStyles from './footer.style'
import { Link } from 'gatsby'
import RandomFooterText from '../../../lib/random-footer-text'

const EasterEgg = props => {
  return <span {...props}>{props.message}</span>
}

EasterEgg.propType = {
  message: PropTypes.string.isRequired,
}

const Footer = ({ classes }) => {
  const [message, updateMessage] = React.useState(RandomFooterText())
  return (
    <footer className={classes.root}>
      <Typography component={'p'} align={'center'} className={classes.inner}>
        {'- '}
        &copy; {new Date().getFullYear()} compose.us GmbH
        {'  '}
        Kontaktiert uns via{' '}
        <a href="mailto:hello@compose.us" className={classes.link}>
          hello@compose.us
        </a>
        {'  '}
        <Link to="/impressum" className={classes.link}>
          Impressum
        </Link>
        {' -'}
        <br />
        <EasterEgg
          message={message}
          onMouseEnter={() => updateMessage(RandomFooterText())}
        />
      </Typography>
    </footer>
  )
}

Footer.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(Footer)
